urcesContent":["\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n@import '@/scss/vars.scss';\n.category {\n  padding: 40px 0 100px;\n  background: #fbf8ec;\n\n  @media(max-width: 1199px) {\n    padding: 30px 0 80px;\n  }\n\n  @media(max-width: 767px) {\n    padding: 20px 0 40px;\n  }\n\n  &__heading {\n    padding-bottom: 50px;\n    display: flex;\n    flex-wrap: wrap;\n    align-items: center;\n    @media(max-width: 1199px) {\n      padding-bottom: 40px;\n    }\n    @media(max-width: 767px) {\n      padding-bottom: 30px;\n    }\n\n    &--space {\n      justify-content: space-between;\n    }\n  }\n\n  &__total {\n    margin-top: -40px;\n    color: #41445C;\n    font-size: 14px;\n    font-weight: 500;\n    position: relative;\n    @media(max-width: 1199px) {\n      margin-top: -30px;\n    }\n    @media(max-width: 991px) {\n      margin-top: -20px;\n    }\n    @media(max-width: 575px) {\n      font-size: 13px;\n      margin-top: -15px;\n    }\n  }\n\n  &__title {\n    color: #41445C;\n    font-size: 50px;\n    font-weight: bold;\n    @media(max-width: 1199px) {\n      font-size: 40px;\n    }\n    @media(max-width: 767px) {\n      font-size: 30px;\n    }\n    @media(max-width: 575px) {\n      font-size: 25px;\n    }\n\n    @media(max-width: 350px) {\n      font-size: 20px;\n    }\n  }\n\n  &__search-val {\n    color: $secondary;\n    margin-left: 15px;\n    font-weight: 400;\n    @media(max-width: 575px) {\n      margin-left: 10px;\n    }\n  }\n\n  &__row {\n    display: flex;\n    flex-wrap: wrap;\n    margin: 0 -20px;\n    @media(max-width: 1199px) {\n      margin: 0 -10px;\n    }\n    @media(max-width: 575px) {\n      margin: 0;\n      flex-direction: column;\n    }\n  }\n\n  &__item {\n    width: 100%;\n    max-width: calc(25% - 40px);\n    margin: 20px;\n\n    @media(max-width: 1440px) {\n      max-width: calc(33.333% - 40px);\n    }