 background: #FFF;\n\n  $root: &;\n\n  &__row {\n    display: flex;\n    flex-wrap: wrap;\n  }\n\n  &__slide {\n    width: 100%;\n    max-width: calc(100% - 320px);\n    position: relative;\n\n    @media(max-width: 991px) {\n      max-width: calc(100% - 270px);\n    }\n\n    @media(max-width: 767px) {\n      max-width: 100%;\n    }\n  }\n\n  &__slide-item {\n    display: flex;\n    align-items: center;\n    width: 100%;\n    position: relative;\n    min-height: 430px;\n    border-radius: 15px;\n    overflow: hidden;\n    @media(max-width: 991px) {\n      min-height: 420px;\n    }\n\n    @media(max-width: 767px) {\n      min-height: 350px;\n    }\n  }\n\n  &__slide-img {\n    max-width: none;\n    width: 100%;\n    height: 100%;\n    object-fit: cover;\n    object-position: center;\n    position: absolute;\n    left: 0;\n    top: 0;\n  }\n\n  &__slide-content {\n    margin: 30px;\n    background: rgba(255,255,255,.8);\n    border-radius: 15px;\n    padding: 30px 30px;\n    max-width: 500px;\n    position: relative;\n\n    @media(max-width: 1199px) {\n      margin: 20px;\n      padding: 20px;\n    }\n\n    @media(max-width: 767px) {\n      margin: 10px;\n      padding: 10px 15px;\n    }\n  }\n\n  &__slide-title {\n    color: $primary;\n    font-size: 40px;\n    font-weight: 700;\n    margin-bottom: 8px;\n\n    @media(max-width: 1199px) {\n      font-size: 35px;\n    }\n\n    @media(max-width: 767px) {\n      font-size: 30px;\n    }\n\n    @media(max-width: 350px) {\n      font-size: 20px;\n    }\n  }\n\n  &__slide-descr {\n    font-size: 16px;\n    font-weight: 300;\n    color: $primary;\n    margin-bottom: 10px;\n    @media(max-width: 1199px) {\n      font-size: 15px;\n    }\n    @media(max-width: 767px) {\n      font-size: 14px;\n    }\n  }\n\n  &__slide-price {\n    color: $red;\n    font-size: 35px;\n    font-weight: 700;\n    margin-bottom: 5px;\n    @media(max-width: 1199px) {\n      font-size: 30px;\n    }\n    @media(max-width: 767px) {\n      font-size: 25px;\n    }\n  }\n\n  &__slide-link {\n    color: #fff;\n    font-size: 16px;\n    font-weight: 500;\n    background: $yellow;\n    border-radius: 25px;\n    padding: 12px 32px 13px;\n    margin-top: 15px;\n    display: inline-flex;\n    align-items: center;\n    justify-content: center;\n    @media(max-width: 1199px) {\n      font-size: 15px;\n    }\n  }\n\n  &__arrow {\n    position: absolute;\n    bottom: 30px;\n    width: 38px;\n    height: 38px;\n    display: inline-flex;\n    align-items: center;\n    justify-content: center;\n    border: 2px solid #FFF;\n    border-radius: 19px;\n    z-index: 1;\n    cursor: pointer;\n\n    @media(max-width: 767px) {\n      bottom: 15px;\n    }\n\n    &--prev {\n      right: 80px;\n\n      @media(max-width: 767px) {\n        right: 65px;\n      }\n\n      #{$root} {\n        &__arrow-ico {\n          margin-right: 2px;\n        }\n      }\n    }\n\n    &--next {\n      right: 30px;\n\n      @media(max-width: 767px) {\n        right: 15px;\n      }\n\n      #{$root} {\n        &__arrow-ico {\n          margin-left: 2px;\n        }\n      }\n    }\n  }\n\n  &__arrow-ico {\n    color: #FFF;\n    width: 20px;\n    height: 20px;\n  }\n\n  &__banner {\n    width: 100%;\n    max-width: 290px;\n    margin-left: 30px;\n\n    @media(max-width: 991px) {\n      max-width: 250px;\n      margin-left: 20px;\n    }\n\n    @media(max-width: 767px) {\n      max-width: 100%;\n      margin: 20px 0 0;\n      display: flex;\n      flex-wrap: wrap;\n      justify-content: space-between;\n    }\n  }\n\n  &__banner-item {\n    border-radius: 15px;\n    padding: 20px 23px 18px 30px;\n    z-index: 5;\n    margin-bottom: 30px;\n    overflow: hidden;\n    height: 200px;\n    background: #ccc;\n    position: relative;\n\n    &:last-child {\n      margin-bottom: 0;\n    }\n\n    @media(max-width: 1199px) {\n      padding: 20px;\n    }\n\n    @media(max-width: 991px) {\n      margin-bottom: 20px;\n    }\n\n    @media(max-width: 767px) {\n      width: 100%;\n      max-width: calc(50% - 10px);\n    }\n\n    @media(max-width: 575px) {\n      max-width: 100%;\n    }\n  }\n\n  &__banner-title {\n    color: #fff;\n    font-size: 24px;\n    font-weight: 700;\n    margin-bottom: 5px;\n    z-index: 1;\n    position: relative;\n    @media(max-width: 991px) {\n      font-size: 20px;\n    }\n  }\n\n  &__banner-descr {\n    color: #fff;\n    font-size: 16px;\n    font-weight: 300;\n    max-height: 62px;\n    overflow: hidden;\n    z-index: 1;\n    position: relative;\n    @media(max-width: 991px) {\n      font-size: 14px;\n    }\n  }\n\n  &__banner-price {\n    margin-top: 5px;\n    color: #fff;\n    font-size: 35px;\n    font-weight: 700;\n    z-index: 1;\n    position: relative;\n  }\n\n  &__banner-img {\n    position: absolute;\n    top: 0;\n    left: 0;\n    width: 100%;\n    height: 100%;\n    max-width: none;\n    object-fit: cover;\n  }\n\n  &__banner-link {\n    position: absolute;\n    left: 0;\n    top: 0;\n    z-index: 3;\n    width: 100%;\n    height: 100%;\n  }\n\n  &__banner-btn {\n    position: absolute;\n    right: 20px;\n    bottom: 20px;\n    text-decoration: none;\n    background: $blue;\n    color: #fff;\n    font-size: 20px;\n    font-weight: 700;\n    padding: 10px 50px 10px 25px;\n    border-radius: 25px;\n    display: inline-flex;\n    align-items: center;\n    justify-content: center;\n    @media(max-width: 1199px) {\n      font-size: 18px;\n    }\n  }\n\n  &__banner-ico {\n    width: 24px;\n    height: 24px;\n    border: 2px solid #FFF;\n    border-radius: 12px;\n    top: calc(50% - 10px);\n    right: 10px;\n    position: absolute;\n    display: inline-flex;\n    align-items: center;\n    justify-content: center;\n  }\n\n  &__banner-arrow {\n    width: 12px;\n    height: 12px;\n    color: #FFF;\n    margin-left: 2px;\n  }\n}\n","$primary: #484848;\n$secondary: #a2a1a1;\n$blue: #a993dc;\n$red: #ef726b;\n$green: #7dca3f;\n$brown: #d88861;\n$yellow: #ffad0d;\n$shadow: rgb(0, 0, 0, .10);\n$borderColor: #e6e6e6;\n